

.spinner {
    position: absolute;
    z-index: 2;
    padding-top: 44.5vh;
    padding-left: 44.5vw;
    perspective: 200px;
  }
  
  i {
    display: block;
    position: absolute;
    opacity: 1;
  }
  i b {
    display: block;
    width: 6px;
    height: 6px;
    border-radius: 6px;
    background: white;
    box-shadow: 0px 0px 14px white;
    animation-name: spin;
    animation-duration: 1s;
    animation-iteration-count: infinite;
    animation-timing-function: ease-in-out;
  }
  
